@use '../inc/vars' as *;
@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(0,0,0, 0.99);
  }
  99% {
      -webkit-box-shadow: 0 0 0 20px rgba(0,0,0, 0);
  }
  100% {
      -webkit-box-shadow: 0 0 0 0 rgba(0,0,0, 0);
  }
}
@keyframes pulse {
  0% {
    -moz-box-shadow: 0 0 0 0 rgba(0,0,0, 0.99);
    box-shadow: 0 0 0 0 rgba(0,0,0, 0.4);
  }
  99% {
      -moz-box-shadow: 0 0 0 20px rgba(0,0,0, 0);
      box-shadow: 0 0 0 20px rgba(0,0,0, 0);
  }
  100% {
      -moz-box-shadow: 0 0 0 0 rgba(0,0,0, 0);
      box-shadow: 0 0 0 0 rgba(0,0,0, 0);
  }
}
.map {
  align-items: initial;
  background: $green;
  padding-bottom: 0;
  min-height: auto;
  .container {
    position: relative;
  }
  .section {
    &__header {
  
    }
    &__title {

    }
    &__header-additional {

    }
    &__subtitle {

    }
    &__body {
      width: 100%;
      display: flex;
    }
  }
  &__links {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    .map__bg {
      opacity: 1;
    }
  }
  &__link {
    display: flex;
    position: absolute;
    box-sizing: content-box;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    transition: 0s 0.3s transform;

    &::before {
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      position: absolute;
      content: '';
      width: 10px;
      height: 10px;
      box-sizing: border-box;
      border-radius: 100px;
      background: $text;
      opacity: 1;
      box-shadow: 0 0 0 rgba(0,0,0, 0.9);
      animation: pulse 1s infinite;
      transition: opacity 0s 0.3s;
    }
    &--small {
      
    }
    &--large {
      &:hover {
        @media(max-width: $lg){
          width: 100px;
          height: 100px;
          transform: translate(-30px, -30px) !important;
        }
      }
      .map__link-img {
        @media(max-width: $lg){
          width: 100px;
          height: 100px;
          
        }
      }
    }
    &:hover {
      z-index: 1;
      width: 173px;
      height: 173px;
      transition: 0s 0s transform;
      transform: translate(-66px, -66px);
      @media(max-width: $xl){
        width: 100px;
        height: 100px;
        transform: translate(-30px, -30px);
      }
      @media(max-width: $lg){
        width: 60px;
        height: 60px;
        transform: translate(-10px, -10px);
      }
      @media(max-width: $md){
        
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
      &::before {
        opacity: 0;
        transition: opacity 0s 0s;
      }
      .map__link-img {
        transform: scale(1);
        // left: 0;
        // top: 0;
      }
    }
  }
  &__link-img {
    background-color: $text;
    color: $text-light;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: absolute;
    // left: -66px;
    // top: -66px;
    display: flex;
    max-width: none;
    transform: scale(0);
    transition: all 0.3s;
    width: 173px;
    height: 173px;
    border-radius: 173px;
    overflow: hidden;
    text-transform: uppercase;
    font-size: 18px;
    line-height: 1;
    @media(max-width: $xl){
      width: 100px;
      height: 100px;
      font-size: 13px;
    }
    @media(max-width: $lg){
      width: 60px;
      height: 60px;
      
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
    &:hover {
      
      transform: scale(1);
    }
  }
  &__text {
    height: 220px;
    border-left: 1px solid;
    padding-left: 50px;
    max-width: 370px;
    position: absolute;
    bottom: 0;
    right: 0;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
