    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    &--right {
      right: -270px;
      @media(max-width: $xl){
        // right: -190px;
        right: -120px;
      }
      @media(max-width: $md){
        right: 0;
      }
    }
  }
  
  &__bg {
    max-width: 100%;
    height: auto;
    opacity: 0;
    // position: absolute;
    
  }
